import Vue from 'vue'
import App from './App.vue'
import router from './router'
import filters from './filter'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import axios from 'axios'
import 'babel-polyfill'
import {Row, Col, Layout, Header, Content, Footer, Button, Carousel, 
  CarouselItem, Menu, Submenu, MenuGroup, MenuItem, Scroll, Input,
  Spin, Icon, Slider, Breadcrumb, BreadcrumbItem, Page, Card, Modal,Notice,Tag, Select,Option} from 'iview';
Vue.component('Row', Row);
Vue.component('i-col', Col);
Vue.component('Layout', Layout);
Vue.component('Header', Header);
Vue.component('Content', Content);
Vue.component('Footer', Footer);
Vue.component('Menu', Menu);
Vue.component('Submenu', Submenu);
Vue.component('MenuGroup', MenuGroup);
Vue.component('MenuItem', MenuItem);
Vue.component('Button', Button);
Vue.component('Carousel',Carousel);
Vue.component('CarouselItem',CarouselItem);
Vue.component('i-input', Input);
Vue.component('Scroll', Scroll);
Vue.component('Spin', Spin);
Vue.component('Icon', Icon);
Vue.component('Slider', Slider);
Vue.component('Breadcrumb', Breadcrumb);
Vue.component('BreadcrumbItem', BreadcrumbItem);
Vue.component('Page', Page);
Vue.component('Card', Card);
Vue.component('Modal', Modal);
Vue.component('Notice',Notice);
Vue.component('Tag', Tag);
Vue.component('i-select', Select);
Vue.component('i-option', Option);

import 'iview/dist/styles/iview.css'
import 'swiper/dist/css/swiper.css'

Vue.use(VueAwesomeSwiper, /* { default global options } */)
Vue.config.productionTip = true
Vue.prototype.$http= axios
Vue.prototype.$Notice = Notice
Vue.prototype.$Spin = Spin
Object.keys(filters).forEach((key) => {
  Vue.filter(key, filters[key]);
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



